import { useState } from "react"
import { Button, Form, Col } from "react-bootstrap"
import { Controller, useForm } from "react-hook-form"
import { Redirect } from "react-router"
import { RegionalesTypeahead } from "../../../../commons/components/RegionalesTypeahead"
import * as rules from '../../../../commons/components/rules'
import { Regional } from "../../../../commons/services/RegionalesService"

type Inputs = {
  nit: string,
  nombre: string,
  regional: Regional[]
}

export const ProveedorEmpresaForm = ()=>{

  const [regionales, setRegionales] = useState<Regional[]>([])
  const [redirect, shouldRedirect] = useState(false)
  const {
    handleSubmit,
    control
  } = useForm<Inputs>()

  if(redirect){
    return <Redirect to="/clinica/proveedores/1/contacto"></Redirect>
  }

  return <Form onSubmit={handleSubmit((values)=>{

  })}>
    <Form.Row>
      <Form.Group as={Col} sm={4}>
        <Form.Label>NIT</Form.Label>
        <Form.Control/>
      </Form.Group>
      <Form.Group as={Col} sm={8}>
        <Form.Label>Nombre</Form.Label>
        <Form.Control/>
      </Form.Group>
    </Form.Row>
    <Form.Row>
      <Form.Group as={Col} md={4}>
        <Form.Label>Regional</Form.Label>
        <Controller
          name="regional"
          control={control}
          // rules={{
          //   required: rules.required()
          // }}
          render={({field, fieldState})=>{
            return <>
              <RegionalesTypeahead
                id="medicos-form/regionales-typeahead"
                onLoad={(regionales)=>setRegionales(regionales)}
                className={fieldState.error ? "is-invalid" : ""}
                isInvalid={!!fieldState.error}
                selected={field.value}
                onChange={field.onChange}
                onBlur={field.onBlur}
              />
              <Form.Control.Feedback type="invalid">{fieldState.error?.message}</Form.Control.Feedback>
            </>
          }}
        />
      </Form.Group>
    </Form.Row>
    <Button
      type="submit"
      onClick={()=>{
        shouldRedirect(true)
      }}
    >
      Guardar
    </Button>
  </Form>
}